.projectModal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;

  color: #1e2124;
  background-color: rgba(0, 0, 0, 0.5);
  display: grid;
  place-items: center;
}

.projectModal_content {
  position: relative;

  background-color: #fefefe;
  border: 1px solid #888;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  padding: 20px;

  max-width: 400px;
  max-height: 90%;

  overflow: auto;
  scrollbar-width: thin;
  scrollbar-color: transparent transparent;
}

@media only screen and (max-width: 440px) {
  .projectModal_content {
    max-width: 90%;
  }
}

.close {
  position: absolute;
  right: 20px;
  border: none;
  cursor: pointer;
  font-size: 18px;
}

.redAlert {
  color: red;
}

.projectModal h2 {
  text-align: center;
  font-size: 24px;
  margin: 0 0 16px;
}

.projectModal p {
  margin-bottom: 8px;
}

strong {
  font-weight: bold;
}

.projectModal ul {
  margin-top: 0;
  margin-bottom: 16px;
  padding-left: 20px;
}

.projectModal li {
  list-style-type: disc;
  margin-bottom: 4px;
}

.projectModal h3 {
  font-size: 18px;
  margin-bottom: 12px;
}

.projectModal_content::-webkit-scrollbar {
  display: none;
}

.projectModal_content {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
